}

.card-content{
  width: 63px;
  height: 90px; 
  transform-style: preserve-3d;
  transition: transform 2s;
}

.flip {
  transform: rotateY(180deg);
}

.card-front, .card-back {
  -webkit-backface-visibility: hidden; /* for Safari 5.1+ and iOS 5+ */
  backface-visibility: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
}

.card-front {
  transform: rotateY(180deg);
}
.card-content img{
  width: 100%;
  height: 100%;
  border-radius: 7px;
}